import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Tj from '@/components/tj'
import Sp from '@/components/sp'
import Rd from '@/components/rd'
import Sh from '@/components/sh'
import Yl from '@/components/yl'
import Xf from '@/components/xf'
import Sx from '@/components/sx'
import Ss from '@/components/ss'
import Tig from '@/components/tig'



Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
    	path:'/tj',
    	component:Tj
    },
    {
    	path:'/sp',
    	component:Sp
    },
    {
    	path:'/rd',
    	component:Rd
    },
    {
    	path:'/sh',
    	component:Sh
    },
    {
    	path:'/yl',
    	component:Yl
    },
    {
    	path:'/xf',
    	component:Xf
    },
    {
    	path:'/sx',
    	component:Sx
    },
    {
    	path:'/ss',
    	component:Ss
    },
    {
    	path:'/tig',
    	compont:Tig
    }
    
  ]
})



